/*=====================
        GLOBALS
=====================*/

body {
  background: #fff;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

hr {
  max-width: 65%;
  color: #3fbfad;
  border: 1px dotted;
}

/*=====================
        LAYOUT
=====================*/

.wrapper {
  background: #fff;
}

/* Flexbox */

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 10px;
}

.flex {
  -ms-flex: 1 0 150px;
      flex: 1 0 150px;
  margin: 10px;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

/*=====================
    COMPONENT STYLES
=====================*/

.heading {
  text-align: center;
  font-weight: lighter;
  font-size: 1.5em;
  margin-bottom: 5px;
}

/* Form */

.form {
  text-align: center;
  margin: 10px;
}

input[type="text"],
textarea,
button {
  width: 100%;
  font-size: 1.25rem;
  margin-bottom: 10px;
  border-radius: 2px;
  outline: none;
}

input[type="text"] {
  height: 55px;
  border: none;
  background-color: #f2f2f2;
}

textarea {
  height: 85px;
  border: none;
  background-color: #f2f2f2;
  resize: none;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #90949c;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #90949c;
}

input::placeholder,
textarea::placeholder {
  color: #90949c;
}

button {
  height: 55px;
  border: 2px solid #3fbfad;
  background-color: transparent;
  color: #3fbfad;
  border-radius: 2px;
}

button:hover {
  background-color: #3fbfad;
  color: #fff;
}

.btn-back {
  height: 35px;
  width: 65px;
}

.photo-overlay {
  font-size: 1.5rem;
}

/* Transitions & Animations */

.photo {
  position: relative;
  overflow: hidden;
}

.photo img {
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -o-transition: transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}

.photo:hover img {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}

.photo-overlay {
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 10px;
  background: rgba(0,0,0, .75);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.photo-overlay {
	opacity: 0;
	-webkit-transition: opacity .5s;
	-o-transition: opacity .5s;
	transition: opacity .5s;
}

.photo-overlay:hover {
	opacity: 1;
}

/* Overlay Text Styles */

.detail-heading,
.detail-heading_overlay {
  font-weight: bolder;
  font-style: italic;
  color: #3fbfad;
  margin: 0;
}

.detail-heading_overlay {
  font-size: 2rem;
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg)
}

.detail-heading_overlay:hover,
.remove:hover {
  color: #fff;
  cursor: pointer;
}

.remove {
  position: absolute;
  top: -20px;
  right: 10px;
  font-size: 1.25rem;
  color: #3fbfad;
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

/* Add Movie Prompt Styles */

.add-movie {
  text-align: center;
  margin: 20%;
  font-size: 2rem;
  color: #3fbfad;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
}


@media screen and (min-width: 768px) {

  button {
    width: 50%;
  }

  input[type="text"],
  textarea {
    width: 50%;
    font-size: 1rem;
  }

  .flex {
    max-width: 50%;
  }

  .detail-heading {
    font-size: 1.5rem;
  }

  .detail-text {
    font-size: 1rem;
    font-weight: lighter;
  }

  .add-movie {
    text-align: center;
    -webkit-transform: rotate(-10deg);
        -ms-transform: rotate(-10deg);
            transform: rotate(-10deg);
    font-size: 2rem;
  }

}

@media screen and (min-width: 960px) {

  body {
    background: #e9ebee;
  }

  .wrapper {
    -ms-flex: 1 0 0%;
        flex: 1 0 0%;
    -ms-flex-item-align: baseline;
        align-self: baseline;
    max-width: 768px;
    margin: 25px 10px;
    border-radius: 2px;
    -webkit-box-shadow: 0px 1px 1px 0px #aba;
            box-shadow: 0px 1px 1px 0px #aba;
  }

}



